/****************************************************
	Desing
*****************************************************/

.pic-design, .pic-work {
  display: block;
}

.pic-design{
  animation-name: skew-in;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  margin: 0 auto 15px auto;
  opacity: 0;
  width: 200px;
}
@keyframes skew-in {
  0% {
    opacity: 0;
    transform: scale(0.0) translate(100%, 100px) skewX(20deg);
  }
  100% {
    opacity: 1;
    transform: scale(0.9) translate(0%, 0px) skewX(0deg);
  }
}
.dl1 {
  animation-delay: 0.1s;
}
.dl2 {
  animation-delay: 0.2s;
}
.dl3 {
  animation-delay: 0.3s;
}
.dl4 {
  animation-delay: 0.4s;
}
.dl5 {
  animation-delay: 0.5s;
}
.dl6 {
  animation-delay: 0.6s;
}
.dl7 {
  animation-delay: 0.7s;
}

.comment-design {
  margin-bottom: 50px;
}

.explanation {
  margin-left: 25px;
}

/****************************************************
	Work
*****************************************************/

.pic-work {
  display: block;
}

.image {
  animation-name: role-in;
  animation-delay: 0.3s;
  animation-duration: 1.2s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0, 0, 0.58, 1.0);
  background: #eeeeee;
  border-radius: 10px;
  flex: 1;
  height: 300px;
  margin-bottom: 30px;
  opacity: 0;
  overflow: hidden;
  position: relative;
}
.image:hover .pic-title {
  transform: translateY(-30px);
}
.image:hover .pic-explanation {
  opacity: 1;
  transform: translateY(-40px);
}
.image:hover .more {
  bottom: 0;
}

@keyframes role-in {
  0% {
    opacity: 0;
    transform: translateX(100%) rotate(0deg);
  }
  100% {
    opacity: 1;
    transform: translateX(0%) rotate(-360deg);
  }
}

.pic-work {
  height: 100%;
  margin: 0 auto;
  opacity: 0.8;
  position: relative;
  transition: 0.6s;
  transition-property: opacity;
  vertical-align: top;
  width: 100%;
}
.pic-work:hover {
  opacity: 1;
}

.pic-details {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 1;
}

.pic-title {
  font-family: cursive;
  font-size: 1.8rem;
  font-weight: 900;
  line-height: 2rem;
  margin-top: 130px;
  text-align: center;
  transition: 0.4s;
  transition-property: transform;
  color: #fff;
  text-shadow:2px 2px 2px #000, -1px -1px 2px #000,
              -1px 2px 2px #000, 2px -1px 2px #000,
              0px 2px 2px #000,  0 -1px 2px #000,
              -1px 0 2px #000, 2px 0 2px #000;
}

.pic-explanation {
  margin: 30px 30px 0 30px;
  opacity: 0;
  text-align: center;
  transition: 0.6s;
  transition-property: opacity, transform;
  text-shadow:2px 2px 1px #000, -1px -1px 1px #000,
              -1px 2px 1px #000, 2px -1px 1px #000,
              0px 2px 1px #000,  0 -1px 1px #000,
              -1px 0 1px #000, 2px 0 1px #000;
}

.more {
  background: #1c1c1c;
  padding: 15px;
  position: absolute;
  bottom: -60px;
  transition: 0.6s;
  transition-property: bottom;
  width: 100%;
}

.link-work {
  color: #F3F3FA;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
}
.link-working {
  color: #F3F3FA;
  font-weight: 500;
}
.link-work:hover {
  opacity: 0.6;
  text-decoration: underline;
}

